<style scoped>

</style>

<template>
  <ul class="list" :class="{inline: inline}">
    <li v-for="item in items">
      <a :href="item.page" :class="[]">{{ item.text }}<span class="caret" v-if="item.children && item.children.length"></span></a>
      <ul class="list-inner" :class="{inline: inline}" v-if="item.children && item.children.length">
        <li v-for="child in item.children">
          <a :href="child.page" :class="[]">{{ child.text }}</a>
        </li>
      </ul>
    </li>
  </ul>
</template>

<script lang="babel">
export default {
  props: {
    items: Array,
    inline: Boolean,
  }
}
</script>



