<style scoped>

</style>

<template>
<div class="input">
  <!-- <input type="text" name="tag[]"> -->
  <badge v-for="tag in tags">{{ tag }}<span @click.stop="remove($index)">&times;</span></badge>
  <input v-model="currentTag" v-el:in type="text" class="border-0" @blur="add" :keyup.188="addAndFocus">
</div>
</template>

<script lang="babel">
// aquele componente que cria tags quando digita vírgula ou no blur
import Badge from './badge'

export default {
  props: {
    tags: {
      type: Array,
      twoWay: true,
    },
  },
  components: {
    Badge
  },
  data(){
    return {
      currentTag: ''
    }
  },
  methods: {
    add(){
      this.currentTag && this.tags.push(this.currentTag);
      this.$emit('tag-added', this.currentTag);
      this.currentTag = "";
    },
    addAndFocus(e){
      console.log(e);
      e.preventDefault();
      this.add();
      this.$nextTick(() => this.$els.in.focus())
    },
    remove(index){
      this.tags.splice(index, 1);
    }
  }
}
</script>



